<template lang="pug">
    div(class="page")
        Header
        section(class="offers")
            div(class="container")
                div(class="title")
                    h2: span {{ $t("our-offers") }}
                div(class="offers-content")
                    div(class="row")
                        div(class="col-md-12 col-lg-4 box" v-for="offer in OffersData.offers", :key="offer.id")
                            div(class="offers-box wow fadeInUp" data-wow-duration="1s")
                                div(class="offers-image")
                                    img(:src="offer.image", alt="")
                                div(class="offers-text")
                                    p {{ OffersData.body }}
                                div(class="offers-buttons")
                                    a(href="" class="btn hvr-sweep-to-bottom") {{ $t("request-offer") }}
        Footer
</template>

<script>
import { mapState } from "vuex";

// @ is an alias to /src
import Header from "@/components/global/Header.vue";
import Footer from "@/components/global/Footer.vue";

export default {
    name: "OffersPage",
    metaInfo() {
        return {
            title: process.env.VUE_APP_TITLE,
            titleTemplate: `%s | ${this.OffersData.website_title}`,
            meta: [
                { charset: "utf-8" },
                {
                    name: "description",
                    content: this.OffersData.meta_description,
                },
                {
                    name: "keywords",
                    content: this.OffersData.meta_keywords,
                },
            ],
        };
    },
    components: {
        Header,
        Footer,
    },
    mounted() {
        this.$store.dispatch("getOffersData");
    },
    computed: {
        ...mapState(["OffersData"]),
    },
};
</script>
